import React from 'react'
import { socialImgArr } from '../utils/commonUtils'

export default function SocialCard() {
    return (
        socialImgArr.map((item, index) => (
            <div style={{ cursor: 'pointer' }} key={index}>
                <img src={item.iconName} alt={item.name} />
            </div>
        ))
    )
}
