import './Register.scss';
import React, { useState, useEffect } from 'react';
import * as yup from 'yup';
import { useFormik } from 'formik';
import { TextField, Button, Stack, InputAdornment, IconButton, useMediaQuery, Typography, FormControlLabel, Checkbox } from '@mui/material';
import { appapi } from '../../../api/apiClient';
import { showmessage } from '../../../utils/toastr';
import { useDispatch } from 'react-redux';
import { login } from '../../../redux/features/authSlice';
import { useParams } from 'react-router-dom'
import { buttonStyle1 } from '../../../utils/commonStyle';
import { setLoading } from '../../../redux/features/appSlice';
import { encrypt } from '../../../utils/Crpyto';
import { colors } from '../../../utils/colors';
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';
import VisibilityOffOutlinedIcon from '@mui/icons-material/VisibilityOffOutlined';
import mobilewavybg from '../../../assets/images/mobilewavybg.svg'
import diztextlogo from '../../../assets/images/diztextlogo.svg';
import wavesbg from '../../../assets/images/wavesbg.svg';
import SocialCard from '../../../components/SocialCard';
import DetailCard from '../../../components/DetailCard';

/**
 * Validation Schema for register
 */
const validationSchema = yup.object({
  email: yup
    .string('Enter your Email')
    .matches(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/, 'Email is Invalid')
    .required('Email is Required'),
  name: yup
    .string('Enter your Name')
    .min(3, 'Name must minimum of 3 characters')
    .max(100, 'Name must maximum of 100 characters')
    .required('Name is Required')
    .trim(),
  designation: yup
    .string('Enter your Department')
    .required('Organization is Required')
    .max(50, 'Name must maximum of 50 characters')
    .trim(),
  department: yup
    .string('Enter your Department')
    .required('Deparment is Required')
    .max(50, 'Name must maximum of 50 characters')
    .trim(),
  password: yup
    .string('Enter your Password')
    // .min(8, 'Password must be minimum of 6 characters')
    // .max(12, 'Password must be maximum of 12 characters')
    .matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,12}$/, 'Enter a password of minimum 8 characters with a combination of capital letters, small letters, symbols, and numbers.')
    .required('Password is requried')
    .trim(),
  termsandCondition: yup.bool().oneOf([true], 'Accept Terms & Conditions is required')
})

export default function Register() {

  const dispatch = useDispatch()
  const paramsFromUrl = useParams()

  const isMobile = useMediaQuery('(max-width:1000px)');

  const [visibility, setVisibility] = useState(false);
  const [message, setMessage] = useState(null);
  const [data, setData] = useState({
    email: '',
    name: '',
    designation: '',
    department: '',
    password: '',
    termsandCondition: false
  })

  //checkVerificationTokenStatus 
  const handleVerificationToken = async () => {
    dispatch(setLoading(true))
    let params = {
      params: {
        verificationToken: paramsFromUrl.verificationToken
      }
    }
    const response = await appapi.post(`/auth/verifyInviteToken`, {}, params);
    if (response.ok) {
      const data = response.data;
      dispatch(setLoading(false))
      if (data.status === 'success') {
        setData(prev => ({ ...prev, email: data.data.emailId }))
      } else {
        setMessage(data.message)
      }
    } else {
      dispatch(setLoading(false))
      showmessage(`Something Went Wrong!`, 3000, 'error')
    }
  }

  useEffect(() => {
    handleVerificationToken()
  }, []);

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: data,
    validationSchema: validationSchema,
    validateOnBlur: true,
    validateOnChange: true,
    onSubmit: (values) => {
      register(values)
    },
  })

  /**
   * Register User
   * @param {email,name,department, password} obj 
   */
  const register = async (obj) => {
    dispatch(setLoading(true))
    let params = {
      params: {
        verificationToken: paramsFromUrl.verificationToken
      }
    }
    let payload = {
      name: obj.name,
      password: obj.password,
      department: obj.department,
      designation: obj.designation,
      deviceId: '123'
    }
    let dataObj = {}
    const response = await appapi.post(`/auth/signup/invite`, payload, params)
    if (response.ok) {
      const data = response.data;
      dispatch(setLoading(false))
      if (data.status === 'success') {
        showmessage(data.message)
        dataObj = { ...data.data }
        let encryptedData = encrypt(JSON.stringify(data.data))
        localStorage.setItem('diz', encryptedData)
        appapi.setHeader('Authorization', `Bearer ${data.data.accessToken}`);
        dispatch(login(dataObj))
      } else {
        showmessage(data.message, 5000, 'error')
      }
    } else {
      dispatch(setLoading(false))
      showmessage(`Something Went Wrong!`, 3000, 'error')
    }
  }

  const registerForm = ({ handleSubmit, values, handleChange, errors, touched, handleBlur, isValid, dirty }) => {
    return (
      <>
        <Typography className='fw-bold ff-Roboto createHeading' color={colors.primaryTxt}> Sign up </Typography>
        <form onSubmit={handleSubmit} style={{ minHeight: isMobile ? '730px' : '700px' }}>
          <Stack>
            <Stack className='mt-17'>
              <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={colors.blackMain}>Email</Typography>
              <TextField
                fullWidth
                id='email'
                name='email'
                value={values?.email?.toLowerCase().replace(/^\s/g, '') || ''}
                onChange={handleChange}
                onBlur={handleBlur}
                error={touched.email && Boolean(errors.email)}
                helperText={(touched.email || dirty.email) && errors.email}
                disabled={values.email ? true : false}
                InputProps={{
                  classes: {
                    disabled: 'disabled'
                  }
                }}
              />
            </Stack>
            <Stack className='mt-14'>
              <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={colors.blackMain}>Name</Typography>
              <TextField
                fullWidth
                id='name'
                name='name'
                value={values?.name?.replace(/^\s/g, '') || ''}
                onChange={handleChange}
                onBlur={handleBlur}
                error={touched.name && Boolean(errors.name)}
                helperText={(touched.name || dirty.name) && errors.name}
              />
            </Stack>
            <Stack className='mt-14'>
              <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={colors.blackMain}>Designation</Typography>
              <TextField
                fullWidth
                id='designation'
                name='designation'
                value={values?.designation?.replace(/^\s/g, '') || ''}
                onChange={handleChange}
                onBlur={handleBlur}
                error={touched.designation && Boolean(errors.designation)}
                helperText={(touched.designation || dirty.designation) && errors.designation}
              />
            </Stack>
            <Stack className='mt-14'>
              <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={colors.blackMain}>Department</Typography>
              <TextField
                fullWidth
                id='department'
                name='department'
                value={values?.department?.replace(/^\s/g, '') || ''}
                onChange={handleChange}
                onBlur={handleBlur}
                error={touched.department && Boolean(errors.department)}
                helperText={(touched.department || dirty.department) && errors.department}
              />
            </Stack>
            <Stack className='mt-14 passwordField'>
              <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={colors.blackMain}>Password</Typography>
              </div>
              <TextField
                fullWidth
                id='password'
                name='password'
                value={values?.password?.trim() || ''}
                onChange={handleChange}
                onBlur={handleBlur}
                type={visibility ? 'text' : 'password'}
                error={touched.password && Boolean(errors.password)}
                helperText={(touched.password || dirty.password) && errors.password}
                className={values.password ? 'helperTextStyleblack' : 'helperTextStylered'}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position='end'>
                      {
                        <IconButton onClick={() => setVisibility(!visibility)}>
                          {visibility ? <VisibilityOutlinedIcon /> : <VisibilityOffOutlinedIcon />}
                        </IconButton>
                      }
                    </InputAdornment>
                  )
                }}
              />
            </Stack>
            <Stack className='mt-17 checkboxStyle'>
              <FormControlLabel
                required
                control={
                  <Checkbox
                    sx={{
                      '&.Mui-checked': {
                        color: colors.primaryTxt,
                      },
                    }}
                    id='termsandCondition'
                    name='termsandCondition'
                    checked={!!values.termsandCondition}
                    onChange={handleChange} />
                }
                label={<Typography className='fs-12 ff-Roboto fw-regular black'>I agree to the Terms of Service and Privacy Policy</Typography>}
              />
            </Stack>
            <Button sx={buttonStyle1} disabled={!(dirty && isValid)} type='submit'>Create</Button>
          </Stack>
        </form>
      </>
    )
  }

  return (
    <div className='registerComponent' style={{ padding: isMobile ? '16px' : '0', minHeight: isMobile ? 'auto' : '100vh' }}>
      {
        isMobile ?
          <div className='mobileViewContainer' style={{ minHeight: 'calc(100vh - 32px)' }}>
            <div style={{ backgroundImage: `url(${mobilewavybg})`, minHeight: 'calc(100vh - 32px)', backgroundPosition: 'center bottom', backgroundRepeat: 'no-repeat' }}>
              <div className='logoImg'>
                <img alt='dizLogo' src={diztextlogo} />
              </div>
              <div className='innerContainer' style={{ marginTop: message ? '140px' : '28px' }}>

                {message ?
                  <>
                    <Stack>
                      <Typography className='fw-bold ff-Roboto createHeading' color={colors.primaryTxt}> Sign up </Typography>
                      <Typography className='fs-12 ff-Roboto fw-regular' color={colors.blackMain} style={{ marginBottom: '6px' }}>
                        {message}
                      </Typography>
                    </Stack>
                  </>
                  :
                  registerForm({ ...formik })}
              </div>
            </div>
          </div>
          :
          <Stack style={{ maxWidth: '1368px', margin: 'auto', minHeight: '768px' }} direction='row' justifyContent='space-between'>
            <Stack className='leftStack' style={{ width: '50%' }}>
              <div className='logoImg'>
                <img alt='dizLogo' src={diztextlogo} />
              </div>
              <div className='detailContainer' style={{ backgroundImage: `url(${wavesbg})` }}>
                <Stack className='detailsList'>
                  {
                    <DetailCard />
                  }
                </Stack>
                <Stack className='socialContainer' direction='row'>
                  {
                    <SocialCard />
                  }
                </Stack>
              </div>
            </Stack>
            <Stack className='rightStack' style={{ width: '50%' }}>
              <div className='innerContainer' style={{ marginTop: message ? '22.916666666667vh' : '10.916667vh' }}>
                {message ?
                  <>
                    <Stack>
                      <Typography className='fw-bold ff-Roboto createHeading' color={colors.primaryTxt}> Sign up </Typography>
                      <Typography className='fs-12 ff-Roboto fw-regular' color={colors.blackMain} style={{ marginBottom: '6px' }}>
                        {message}
                      </Typography>
                    </Stack>
                  </>
                  :
                  registerForm({ ...formik })
                }
              </div>
            </Stack>
          </Stack>
      }
      {
        isMobile ?
          <Stack className='footer' alignItems='center' direction='row' justifyContent='space-between'>
            <Stack className='socialContainer' direction='row'>
              {
                <SocialCard />
              }
            </Stack>
            <Typography className='fs-10 ff-Roboto fw-medium black dizcopyrights'>© 2022 Do it Zero Inc.</Typography>
          </Stack>
          :
          <Typography className='fs-10 ff-Roboto fw-medium black dizcopyrights'>© 2022 Do it Zero Inc.</Typography>
      }
    </div>
  )
}
